import axios from 'axios';
// const BASE_URL = 'http://skunkworks.ignitesol.com:8000';
const BASE_URL = 'http://gutendex.com';

export function fetchBooks(genre = "", search = "") {
    return axios.request({
        method: 'get',
        url: 'https://cors-anywhere.herokuapp.com/' + BASE_URL + '/books?&search=' + search + '&topic=' + genre + '&mime_type=image',
        headers: { 'Access-Control-Allow-Origin': '*', 'Content-Type': 'application/json' }
    });
}

